.find-user {
  width: 100%;
  background-color: rgba(31, 34, 111, 0.837);
  border: none;
  border-bottom: 1px solid;
  outline: none;
  padding-left: 5px;
  height: 30px;
  color: rgb(224, 217, 217);
}
.find-user::placeholder {
  color: rgb(224, 217, 217);
}

.find-user:focus::placeholder {
  opacity: 0;
}
