form {
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  height: 300px;
  width: 400px;
  align-items: center;
  justify-content: center;
}
.form form input,
.form form button {
  width: 60%;
}
.form form input {
  padding: 10px;
  outline: none;
  border: none;
  border-bottom: 1px solid rgba(128, 128, 128, 0.475);
  background: white;
  margin-bottom: 30px;
  color: rgba(128, 128, 128, 0.942);
}
.form form button {
  border: none;
  background: rgb(146, 146, 250);
  color: white;
  padding: 10px 10px;
  cursor: pointer;
  width: 65%;
}
