.form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150vh;
  background-color: rgba(122, 120, 240, 0.231);
}

.form__have-account {
  margin-top: 15px;
  max-width: 60%;
  font-size: 12px;
  color: rgba(128, 128, 128, 0.942);
}

.form__have-account span,
.form__name {
  color: rgba(128, 128, 128, 0.942);
}
.form__name {
  margin-bottom: 15px;
}

input:focus::placeholder {
  opacity: 0;
}
